.UserDialog-Wrapper{ 
  /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
  /*内容居中*/  display: -ms-flexbox;  display: flex; -ms-flex-pack: center; justify-content: center; -ms-flex-align: center; align-items: center;
  background: hsla(0,0%,0%,0.40);
}
.UserDialog{
   /* border: 1px solid;
    border-radius: 4px;
    min-width: 20em;
    background: white;
    border: 1px solid #aaa;
    box-shadow: 0 0 1em hsla(0,0%,0%,0.35);*/

    width: 450px;
    border: 3px solid #cff0e5;
    background-color: #fff;  
    border-radius: 10px;
  }
  /*.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left: -2em;} */
  .UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer;} 
  .UserDialog .signInOrSignUp > nav { 
    background: #cff0e5;
    padding-top: 20px;
    /* margin-bottom: 30px; */
    margin-bottom:0;
    border-radius: 5px 5px 0 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}
.signInOrSignUp > nav label{
    width: 110px;
    height: 40px;

    width: 100%;
    height: 100%;
    font-size: 25px;
    line-height: 35px;
    padding: 5px 20px;
    cursor: pointer;
  }
  .signInOrSignUp > nav label:hover{
    border-bottom: 1px solid;
  }
  .UserDialog .signInOrSignUp > .panes { 
     /* padding: 0 1em 1em; */
      margin: 10px 45px;
    }
  
  /*.UserDialog .forgotPassword > h3 { padding: 1em; }
  .UserDialog .forgotPassword > form { padding: 0 1em 1em; } */
  .forgotPassword .row{
    margin-top: 10px;
    width:80%;
  }
  .forgotPassword >.row.actions button[type="submit"]{
    width: 9em;
    display: inline-block;

    height: 30px;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
  }
  .forgotPassword a{
    margin-left: 20px;
  }

  .UserDialog form>.row{ display: -ms-flexbox; display: flex; padding: .3em 0; }
  .UserDialog form>.row>label:first-child{
    width: 4em;

  /*border:1px solid red; */
    margin-top:5px;
 }
  .UserDialog form>.row.actions{ padding-left: 4em; display: -ms-flexbox; display: flex; -ms-flex-align: center; align-items: center;}
  .UserDialog form>.row.actions button{ margin-right: .5em; } 
  
  form[name="signIn"] a{
    margin-left: 20px;
  }
  form input {
    display: block;
    width: 100%;
    height: 30px;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
  }

/*.UserDialog button{ font: inherit; padding: .2em; } */
 #submit {
    display: inline-block;
    text-align: center;
    color: #fff;
    background-color: #a4e4cf;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 5px 10px;
    border: none;
    width: 140px;
    height: 40px;
    font-size: 21px;
    margin-top: 10px;
  
    margin-left:40px;   
 }
 


